import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import Modal_top10 from "./Slide_modal_top10";
export default function Top10Slider(props) {
  const { settings, top10Images } = props;
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedImage, setSelectedImage] = useState("");
  const [currentIndex, setCurrentIndex] = useState(null);
  const openModal = (imageUrl, index) => {
    setSelectedImage(imageUrl);
    setIsModalOpen(true);
    setCurrentIndex(index + 1); // 현재 인덱스를 저장
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };
  const defaultSetting = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: false,
    draggable: false, // 드래그 비활성화
  };

  const mergedSettings = { ...defaultSetting, ...settings }; // 기본 설정과 props로 전달된 설정을 병합합니다.

  return (
    <div>
      <Slider className="weekly_top10_all" {...mergedSettings}>
        {top10Images.map((image, index) => (
          <div key={index}>
            <div className="weekly_TOP10">
              <div className="weekly_index">{index + 1}</div>
              <div className="weekly">
                <img
                  src={image}
                  alt="이미지"
                  className="copy_img_250"
                  onClick={() => openModal(image, index)}
                />
              </div>
            </div>
          </div>
        ))}
      </Slider>
      {isModalOpen && (
        <Modal_top10
          imageUrl={selectedImage}
          onClose={closeModal}
          currentIndex={currentIndex}
        />
      )}
    </div>
  );
}
