import { atom, selector } from "recoil";
import { recoilPersist } from "recoil-persist";

const initialToken = localStorage.getItem("TokenAtom");

const { persistAtom } = recoilPersist({
  key: "recoil-persist",
  storage: localStorage, // 또는 sessionStorage 등 다른 저장소를 사용할 수 있습니다.
});
export const TokenAtomState = atom({
  key: "TokenAtom",
  default: initialToken,
  effects_UNSTABLE: [persistAtom], // 이 부분을 추가합니다.
});

export const isLoginSelector = selector({
  key: "isLoginSelector",
  get: ({ get }) => {
    const token = get(TokenAtomState);
    if (token === null || token === undefined) {
      console.log("Token Value:", token); // 토큰이 null 또는 undefined일 때만 콘솔에 출력
    }
    return token !== null && token !== undefined;
  },
});

