import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Slider from "react-slick";
import "../css/home_combine.css";
import { CombinedAppBar } from "../component/CombinedAppbar";
import { Link } from "react-router-dom";
import Top10Slider from "../component/slide_top10";
import { PrevArrow, NextArrow } from "../component/Slide_arrow";

import PostSlider from "../component/slide_post";
import ModalImage from "../component/slide_random";
const Home = () => {
  const image_random = [];
  const random = 70;

  for (let i = 1; i <= random; i++) {
    // Generate the URL for the random image from Unsplash
    const imageLink = `https://source.unsplash.com/random/440×400?girl=${i}`;
    image_random.push(imageLink);
  }

  const videoUrls = [];
  const numVideos = 2;

  for (let i = 1; i <= numVideos; i++) {
    videoUrls.push(`/video/slide_video_${i}.mp4`);
    videoUrls.push(`/video/slide_video_${i}.mp4`);
    videoUrls.push(`/video/slide_video_${i}.mp4`);
    videoUrls.push(`/video/slide_video_${i}.mp4`);
    videoUrls.push(`/video/slide_video_${i}.mp4`);
  }

  const [currentSlide1, setCurrentSlide1] = useState(0);
  const [currentSlide2, setCurrentSlide2] = useState(0);
  const [currentSlide3, setCurrentSlide3] = useState(0);
  const [currentSlide4, setCurrentSlide4] = useState(0);
  const [currentSlide5, setCurrentSlide5] = useState(0);
  const [currentSlide6, setCurrentSlide6] = useState(0);
  useEffect(() => {
    const importAll = (r) => r.keys().map(r);
    const imgPaths = importAll(
      require.context("../../public/img/Top10", false, /\.(png)$/)
    );

    setTop10Images(imgPaths);
  }, []);

  const [top10Images, setTop10Images] = useState([]);
  const setting_banner = {
    autoplay: true,
    autoplaySpeed: 4000, // 이미지 전환 간격
    draggable: true, // 마우스 드래그로 슬라이드 가능하도록 설정
    arrows: false, // 이전/다음 버튼 숨김
    infinite: true, // 무한 루프
  };
  const setting_6 = {
    slidesToShow: 6,
    slidesToScroll: 5,

    nextArrow: currentSlide1 < image_random.length - 6 ? <NextArrow /> : null, // 첫 번째 슬라이드의 NextArrow 설정
    prevArrow: currentSlide1 === 0 ? null : <PrevArrow />, // 첫 번째 슬라이드의 PrevArrow 설정
    afterChange: (index) => setCurrentSlide1(index), // 첫 번째 슬라이드의 현재 슬라이드 인덱스 업데이트
  };
  const setting_6_1 = {
    slidesToShow: 6,
    slidesToScroll: 5,
    initialSlide: 15, // 인덱스 7번부터 시작
    nextArrow: currentSlide2 < image_random.length - 6 ? <NextArrow /> : null, // 첫 번째 슬라이드의 NextArrow 설정
    prevArrow: currentSlide2 === 0 ? null : <PrevArrow />, // 첫 번째 슬라이드의 PrevArrow 설정
    afterChange: (index) => setCurrentSlide2(index), // 첫 번째 슬라이드의 현재 슬라이드 인덱스 업데이트
  };

  const setting_6_2 = {
    slidesToShow: 6,
    slidesToScroll: 5,
    nextArrow: currentSlide3 < videoUrls.length - 6 ? <NextArrow /> : null, // 두 번째 슬라이드의 NextArrow 설정
    prevArrow: currentSlide3 === 0 ? null : <PrevArrow />, // 두 번째 슬라이드의 PrevArrow 설정
    afterChange: (index) => setCurrentSlide3(index), // 두 번째 슬라이드의 현재 슬라이드 인덱스 업데이트
  };
  const setting_6_3 = {
    slidesToShow: 6,
    slidesToScroll: 5,
    initialSlide: 25, // 인덱스 7번부터 시작
    nextArrow: currentSlide4 < image_random.length - 6 ? <NextArrow /> : null, // 첫 번째 슬라이드의 NextArrow 설정
    prevArrow: currentSlide4 === 0 ? null : <PrevArrow />, // 첫 번째 슬라이드의 PrevArrow 설정
    afterChange: (index) => setCurrentSlide4(index), // 첫 번째 슬라이드의 현재 슬라이드 인덱스 업데이트
  };

  const setting_6_4 = {
    slidesToShow: 6,
    slidesToScroll: 5,
    initialSlide: 40, // 인덱스 7번부터 시작
    nextArrow: currentSlide5 < image_random.length - 6 ? <NextArrow /> : null, // 첫 번째 슬라이드의 NextArrow 설정
    prevArrow: currentSlide5 === 0 ? null : <PrevArrow />, // 첫 번째 슬라이드의 PrevArrow 설정
    afterChange: (index) => setCurrentSlide5(index), // 첫 번째 슬라이드의 현재 슬라이드 인덱스 업데이트
  };

  const setting_250 = {
    slidesToShow: 4, // 한 번에 보이는 이미지 숫자
    slidesToScroll: 3, // 한번에 이동할 스크롤 수

    nextArrow: currentSlide6 < top10Images.length - 4 ? <NextArrow /> : null, // 현재 슬라이드가 화면에 보이는 슬라이드 중 가장 오른쪽이 아닐 때만 NextArrow 표시
    prevArrow: currentSlide6 === 0 ? null : <PrevArrow />, // 첫 번째 슬라이드일 때만 PrevArrow 표시
    afterChange: (index) => setCurrentSlide6(index), // 슬라이드가 변경될 때 현재 슬라이드 인덱스 업데이트
  };

  return (
    <div>
      <CombinedAppBar />
      <div className="Main_background">
        <div className="Home_banner">
          <Slider {...setting_banner}>
            <div>
              <div>
                <img
                  src="/img/slide1_sample1.png"
                  alt="이미지"
                  className="slide-img"
                />
                <div className="Banner-container">
                  <div className="Banner-content">
                    <div className="Banner-text1">adriana lima</div>
                    <div className="Banner-text2">더보기</div>
                  </div>
                </div>
              </div>
            </div>
            <div>
              <img
                src="/img/slide1_sample2.png"
                alt="이미지"
                className="slide-img"
              />
              <div className="Banner-container">
                <div className="Banner-content">
                  <div className="Banner-text1">책읽는 흰둥이</div>
                  <div className="Banner-text2">더보기</div>
                </div>
              </div>
            </div>
          </Slider>
        </div>
        <div className="slide_copy">
          <button className="Slider_label" type="button">
            최신 작품<span className="Slider_more">더보기</span>
          </button>
          <ModalImage images={image_random} settings={setting_6}></ModalImage>
        </div>
        <div className="slide_copy">
          <button className="Slider_label" type="button">
            최신 포스트<span className="Slider_more">더보기</span>
          </button>
          <PostSlider videoUrls={videoUrls} settings={setting_6_2} />
        </div>
        <div className="slide_copy">
          <button className="Slider_label" type="button">
            주간 TOP 10 작품<span className="Slider_more">더보기</span>
          </button>
          <Top10Slider
            top10Images={top10Images}
            settings={setting_250}
          ></Top10Slider>
        </div>
        <div className="slide_copy">
          <button className="Slider_label" type="button">
            #aa aa<span className="Slider_more">더보기</span>
          </button>
          <ModalImage images={image_random} settings={setting_6_1}></ModalImage>
        </div>
        <div className="slide_copy">
          <button className="Slider_label" type="button">
            #bb bb<span className="Slider_more">더보기</span>
          </button>
          <ModalImage images={image_random} settings={setting_6_3}></ModalImage>
        </div>
        <div className="slide_copy">
          <button className="Slider_label" type="button">
            #cc cc<span className="Slider_more">더보기</span>
          </button>
          <ModalImage images={image_random} settings={setting_6_4}></ModalImage>
        </div>
      </div>
    </div>
  );
};

export default Home;
