import React from "react";
import { useRecoilValue, useRecoilState } from "recoil";
import { userInfoState } from "../Recoil/userData_atom";
import { CombinedAppBar } from "../component/CombinedAppbar";
import { IsfavInfoState } from "../Recoil/Mypage_atom";
import { IsStarChargeState } from "../Recoil/Mypage_atom";
import { StarAmountClickState } from "../Recoil/Mypage_atom";
import { StarAmountState } from "../Recoil/Mypage_atom";
import { AmountResultState } from "../Recoil/Mypage_atom";
import { IsPayRingState } from "../Recoil/Mypage_atom";
import { IsRealTimeTransferState } from "../Recoil/Mypage_atom";
import { IsPayPalState } from "../Recoil/Mypage_atom";
import { IsfavListState } from "../Recoil/Mypage_atom";
import "../css/home_combine.css";
import { useEffect, useState } from "react"; // 필요한 모듈 임포트

export default function Profile() {
  const userInfo = useRecoilValue(userInfoState);
  const [IsfavInfo, setIsfavInfo] = useRecoilState(IsfavInfoState);
  const [IsfavList, setIsfavList] = useRecoilState(IsfavListState);
  const [AmountResult, setAmountResult] = useRecoilState(AmountResultState);
  const userId = userInfo.user_id || "닉네임 없음";
  const [IsStarCharge, setIsStarCharge] = useRecoilState(IsStarChargeState);
  const [StarAmountClick, setStarAmountClick] =
    useRecoilState(StarAmountClickState);
  const [IsPayRing, setIsPayRing] = useRecoilState(IsPayRingState);
  const calculatedAmountKRW = (AmountResult * 1.1).toLocaleString();
  const [IsRealTimeTransfer, setIsRealTimeTransfer] = useRecoilState(
    IsRealTimeTransferState
  );
  const [modelNames, setModelNames] = useState([]);

  const [IsPayPal, setIsPayPal] = useRecoilState(IsPayPalState);
  // 선택한 금액을 100으로 나눔
  const calculatedAmountUSD = (AmountResult / 100).toLocaleString();
  const [StarAmount, setStarAmount] = useRecoilState(StarAmountState);

  const togglefavInfolist = () => {
    /* setIsfavInfo((prev) => !prev); 토글을 원할 경우*/
    setIsfavInfo(true);
    setIsStarCharge(false); //
    setStarAmountClick(-1);
    setAmountResult(0);
  };

  const toggleIsfavList = () => {
    /* setIsfavInfo((prev) => !prev); 토글을 원할 경우*/
    setIsfavList((prev) => !prev);
  };

  const toggleStarChargeView = () => {
    setIsStarCharge(true);
    setIsfavInfo(false);
  };

  const toggleIsPayRing = () => {
    setIsPayRing(true);
    setIsRealTimeTransfer(false);
    setIsPayPal(false);
  };
  const toggleIsRealTimeTransfer = () => {
    setIsRealTimeTransfer(true);
    setIsPayRing(false);
    setIsPayPal(false);
  };

  const toggleIsPayPal = () => {
    setIsPayPal(true);
    setIsRealTimeTransfer(false);
    setIsPayRing(false);
  };

  const toggleStarAmountButton = (index) => {
    setStarAmountClick(index);
    const selected = StarAmount[index];
    setAmountResult(selected);
  };

  useEffect(() => {
    // 데이터베이스에서 해당 사용자가 팔로우한 모델의 이름 가져오기
    async function fetchFollowedModels() {
      try {
        const response = await fetch(
          `http://localhost:3005/api/user/followedModels?user_id=${userId}`
        );
        if (response.ok) {
          const data = await response.json();
          // 팔로우한 모델의 이름을 배열로 저장
          const names = data.map((model) => model.follower_name);
          setModelNames(names);
        } else {
          console.error("Failed to fetch followed models.");
        }
      } catch (error) {
        console.error("Error fetching followed models:", error);
      }
    }

    // 사용자가 로그인한 경우에만 실행
    if (userId) {
      fetchFollowedModels();
    }
  }, [userId]);
  return (
    <div>
      <CombinedAppBar />
      <div className="Mypage_background">
        <div className="Mypage_buttons">
          <button
            className="Mypage_first_button"
            onClick={togglefavInfolist}
            style={{
              background: IsfavInfo ? "#FEB47B" : "",
              color: IsfavInfo ? "#000000" : "",
            }}
          >
            구독정보
          </button>
          <button
            className="Mypage_first_button"
            onClick={toggleStarChargeView}
            style={{
              background: IsStarCharge ? "#FEB47B" : "",
              color: IsStarCharge ? "#000000" : "",
            }}
          >
            별 충전
          </button>
          <button className="Mypage_first_button">충전/ 사용 내역</button>
        </div>
        <div>
          {IsfavInfo && (
            <div>
              <div className="Mypage_buttons" style={{ marginTop: "40px" }}>
                <button
                  className="Subscribe_button"
                  onClick={toggleIsfavList}
                  style={{
                    background: IsfavList ? "#FEB47B" : "",
                    color: IsfavList ? "#000000" : "",
                  }}
                >
                  구독목록
                </button>
                <button className="Subscribe_button">내 구독자</button>
              </div>
              {IsfavList && (
                <div className="subscribe_models">
                  {modelNames.map((modelName, index) => (
                    <div className="subscribe_model" key={index}>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="120"
                        height="120"
                        viewBox="0 0 120 120"
                        fill="none"
                      >
                        <circle cx="60" cy="60" r="60" fill="#D9D9D9" />
                      </svg>
                      <div className="subscribe_model-nametag">
                        {" "}
                        {modelName} <span>구독자 n명</span>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}
          {IsStarCharge && (
            <div className="Star_background" style={{ marginTop: "40px" }}>
              <div className="Star_background-second">
                <div className="Star_background-third">
                  <div>결제할 상품을 선택해주세요</div>
                  <div className="Star_amount-buttons">
                    {StarAmount.map((value, index) => (
                      <button
                        key={index}
                        className="Star_amount-button"
                        onClick={() => toggleStarAmountButton(index)}
                        style={{
                          background: StarAmountClick === index ? "#FFF" : "",
                          color: StarAmountClick === index ? "#000000" : "",
                        }}
                      >
                        {value.toLocaleString()}
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="25"
                          height="24"
                          viewBox="0 0 25 24"
                          fill="none"
                        >
                          <g clipPath="url(#clip0_4_1149)">
                            <path
                              d="M12.5 21.35L11.05 20.03C5.9 15.36 2.5 12.28 2.5 8.5C2.5 5.42 4.92 3 8 3C9.74 3 11.41 3.81 12.5 5.09C13.59 3.81 15.26 3 17 3C20.08 3 22.5 5.42 22.5 8.5C22.5 12.28 19.1 15.36 13.95 20.04L12.5 21.35Z"
                              fill="#666666"
                            />
                          </g>
                          <defs>
                            <clipPath id="clip0_4_1149">
                              <rect
                                width="24"
                                height="24"
                                fill="white"
                                transform="translate(0.5)"
                              />
                            </clipPath>
                          </defs>
                        </svg>
                      </button>
                    ))}
                  </div>
                </div>

                <div className="Star_background-forth">
                  <div className="Star_amount_result-first">결제금액</div>
                  <div className="Star_amount_result-second">
                    KRW
                    <div>
                      <div>₩ {calculatedAmountKRW}</div>
                      <div className="Star_amount_VAT">VAT 포함</div>
                    </div>
                  </div>
                  <div className="Star_amount_result-second">
                    USD
                    <div>
                      <div>$ {calculatedAmountUSD}</div>
                      <div className="Star_amount_VAT">VAT 포함</div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="Star_background-second">
                <div className="Star_background-fifth">
                  <div>결제정보를 입력해주세요</div>
                  <div className="Payment_buttons">
                    <button
                      className="Payment_button"
                      onClick={toggleIsPayRing}
                      style={{
                        background: IsPayRing ? "#FFF" : "",
                        color: IsPayRing ? "#000000" : "",
                      }}
                    >
                      페이링
                    </button>
                    <button
                      className="Payment_button"
                      onClick={toggleIsRealTimeTransfer}
                      style={{
                        background: IsRealTimeTransfer ? "#FFF" : "",
                        color: IsRealTimeTransfer ? "#000000" : "",
                      }}
                    >
                      실시간 계좌이체
                    </button>
                    <button
                      className="Payment_button"
                      onClick={toggleIsPayPal}
                      style={{
                        background: IsPayPal ? "#FFF" : "",
                        color: IsPayPal ? "#000000" : "",
                      }}
                    >
                      Paypal
                    </button>
                  </div>
                  {IsPayRing && (
                    <form className="Payment_textfields">
                      <div className="Payment_container">
                        <span className="input_title">카드번호</span>
                        <div>
                          <input
                            className="Payment_Card-number"
                            type="text"
                            name="user_id"
                            autoComplete="off"
                            placeholder="카드번호를 입력해주세요"
                          />
                        </div>
                      </div>
                      <div className="Payment_container">
                        <span className="input_title">유효기간</span>
                        <div className="Payment_container_row">
                          <div>
                            <input
                              className="Payment_Validity-date"
                              type="password"
                              name="password"
                              autoComplete="off"
                              placeholder="MM"
                            />
                          </div>
                          <div>
                            <input
                              className="Payment_Validity-date"
                              type="password"
                              name="password"
                              autoComplete="off"
                              placeholder="YY"
                            />
                          </div>
                        </div>
                      </div>
                      <div className="Payment_container">
                        <span className="input_title">
                          생년월일 or 사업자등록번호
                        </span>
                        <div>
                          <input
                            className="Payment_Card-number"
                            type="text"
                            name="user_id"
                            autoComplete="off"
                            placeholder="생년월일 또는 사업자 등록번호를 입력해주세요"
                          />
                        </div>
                      </div>
                      <div className="Payment_container">
                        <span className="input_title">
                          카드 비밀번호 앞 두자리
                        </span>
                        <div>
                          <input
                            className="Paymnent_card-password"
                            type="password"
                            name="user_id"
                            autoComplete="off"
                            placeholder="**"
                          />
                        </div>
                      </div>
                      <button className="Payment_PayRing">결제하기</button>
                    </form>
                  )}
                  {IsRealTimeTransfer && (
                    <div>
                      {" "}
                      <button className="Payment_Paying-realtime">
                        실시간 계좌이체 진행
                      </button>
                    </div>
                  )}
                  {IsPayPal && <div></div>}
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
