import React from "react";
import { useRecoilState } from "recoil";
import { sidebarState } from "../Recoil/sidebar_atom";
import { handleLogout } from "./logout";
import { Link, useNavigate } from "react-router-dom";
import { userInfoState } from "../Recoil/userData_atom";
import { useRecoilValue } from "recoil";

export function CombinedAppBar() {
  const [isSidebarOpen, setIsSidebarOpen] = useRecoilState(sidebarState);
  const userInfo = useRecoilValue(userInfoState);
  const handleModalClick = (e) => {
    if (e.target.classList.contains("blur-effect")) {
      setIsSidebarOpen(false);
    }
  };
  const navigate = useNavigate();

  const goToProfile = () => {
    navigate(`/profile/${userInfo.nickname}`);
  };

  const toggleSidebar = () => {
    setIsSidebarOpen((prev) => !prev);
  };

  const handleClick = () => {
    console.log("사이드바 오픈");
    toggleSidebar();
  };

  return (
    <div>
      <div className="black-nav">
        <div className="nav-content">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="12"
            height="9"
            viewBox="0 0 12 9"
            fill="none"
            onClick={handleClick}
            style={{ cursor: "pointer" }}
          >
            <rect width="12" height="1" fill="#FEB47B" />
            <rect y="4" width="12" height="1" fill="#FEB47B" />
            <rect y="8" width="12" height="1" fill="#FEB47B" />
          </svg>
          <span className="nav-text">
            <Link to="/" className="nav-text">
              RealGravia
            </Link>
          </span>
          <div className="nav-search">
            <img src="/img/search.jpg" alt="이미지" />
            <span style={{ opacity: "30%", color: "#FFFFFF" }}>검색</span>
          </div>
        </div>
      </div>
      {isSidebarOpen && (
        <div className="blur-effect" onClick={handleModalClick}>
          <div className="Sidebar_content">
            <div className="Sidebar_top ">
              <div className="Profile_container">
                <div className="Profile_main">
                  <div className="Profile_image">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="62"
                      height="62"
                      viewBox="0 0 62 62"
                      fill="none"
                    >
                      <circle cx="31" cy="31" r="31" fill="#D9D9D9" />
                    </svg>
                  </div>
                  <div className="Profile_nickname">{userInfo.nickname}</div>{" "}
                </div>
              </div>
              <div className="DM-button">DM</div>
            </div>
            <div className="Sidebar_second">
              현재 별
              <div className="Cash_container">
                <span>0</span>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                >
                  <g clipPath="url(#clip0_4_1270)">
                    <path
                      d="M12 21.35L10.55 20.03C5.4 15.36 2 12.28 2 8.5C2 5.42 4.42 3 7.5 3C9.24 3 10.91 3.81 12 5.09C13.09 3.81 14.76 3 16.5 3C19.58 3 22 5.42 22 8.5C22 12.28 18.6 15.36 13.45 20.04L12 21.35Z"
                      fill="#666666"
                    />
                  </g>
                  <defs>
                    <clipPath id="clip0_4_1270">
                      <rect width="24" height="24" fill="white" />
                    </clipPath>
                  </defs>
                </svg>
              </div>
            </div>
            <div className="Sidebar_all-buttons">
              <div className="Sidebar_buttons">
                <button className="mypage_button" onClick={goToProfile}>
                  마이페이지
                </button>
                <button className="wishlist_button">찜한작품</button>
                <button className="purchases">보유작품</button>
              </div>
              <div className="Sidebar_middle"></div>
              <div className="Sidebar_bottom-buttons">
                <div className="Sidebar_bottom-button">모델 지원</div>
                <div className="Sidebar_bottom-button">고객 문의</div>
                <div className="Sidebar_bottom-button" onClick={handleLogout}>
                  로그아웃
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
