import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import Modal from "./Slide_modal";

export default function ModalImage(props) {
  const { settings } = props; // props로부터 설정 객체를 받아옵니다.
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedImage, setSelectedImage] = useState("");

  const openModal = (imageLinkone) => {
    setSelectedImage(imageLinkone);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };
  const defaultSetting = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: false,
    draggable: false, // 드래그 비활성화
  };
  const images = props.images || []; // props로 전달된 이미지 배열 또는 빈 배열 사용

  const mergedSettings = { ...defaultSetting, ...settings }; // 기본 설정과 props로 전달된 설정을 병합합니다.

  return (
    <div>
      <Slider className="slide_copy-imgs" {...mergedSettings}>
        {images.map((urlone, index) => (
          <div key={index}>
            <img
              src={urlone}
              alt="이미지"
              className="copy_img_120"
              onClick={() => openModal(urlone)}
            />
          </div>
        ))}
      </Slider>
      {isModalOpen && <Modal imageUrl={selectedImage} onClose={closeModal} />}
    </div>
  );
}
