import "../css/home_combine.css";

import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Select from "react-select";
import axios from "axios";

const Resister = () => {
  const navigate = useNavigate();
  const inputLabels = {
    user_id: "아이디",
    password: "비밀번호",
    name: "이름",
    phone: "전화번호",
    nickname: "닉네임",
    email: "이메일",
  };
  const initialInputs = {
    user_id: "",
    password: "",
    name: "",
    phone: "",
    nickname: "",
    email: "",
  };
  const [inputs, setInputs] = useState(initialInputs);
  const [errors, setErrors] = useState({});

  const validateInputs = () => {
    const newErrors = {};

    // user_id 유효성 검사
    if (!inputs.user_id.match(/^[a-zA-Z0-9!@]{1,10}$/)) {
      newErrors.user_id =
        "아이디는 10자 이하의 영어 대소문자, 숫자, ! 또는 @만 가능합니다.";
    }

    // password 유효성 검사
    if (inputs.password.length > 16 || inputs.password.length === 0) {
      newErrors.password = "비밀번호는 16자 이하로 입력해주세요.";
    }

    // name 유효성 검사
    if (!inputs.name.match(/^[가-힣a-zA-Zㄱ-ㅎㅏ-ㅣ\s]{1,10}$/)) {
      newErrors.name =
        "이름은 한글, 영어, 공백을 포함하여 10자 이하로 입력해주세요.";
    }

    // phone 유효성 검사
    if (!inputs.phone.match(/^\d{1,11}$/)) {
      newErrors.phone = "전화번호는 숫자로 11자 이하로 입력해주세요.";
    }

    // nickname 유효성 검사
    if (inputs.nickname.length > 16 || inputs.password.length === 0) {
      newErrors.nickname = "닉네임은 11자 이하로 입력해주세요.";
    }

    // email 유효성 검사
    if (!inputs.email.match(/^[\w.-]+@[A-Za-z]+\.[A-Za-z]+$/)) {
      newErrors.email = "올바른 이메일 주소를 입력해주세요.";
    }

    setErrors(newErrors);

    const isValid = Object.keys(newErrors).length === 0;
    return isValid;
  };
  const handleInputChange = (e) => {
    const { name, value } = e.target;

    // input 값이 변경될 때마다 콘솔에 출력
    console.log(`${name}: ${value}`);

    // phone 입력 필드의 경우 숫자만 허용
    if (name === "phone" && !isNaN(value)) {
      setInputs({
        ...inputs,
        [name]: value,
      });
    } else if (name !== "phone") {
      setInputs({
        ...inputs,
        [name]: value,
      });
    }
  };

  const languageOptions = [
    { value: "korean", label: "한국어" },
    { value: "english", label: "English" },
    { value: "japanese", label: "日本語" },
  ];

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (validateInputs()) {
      try {
        // 사용자 데이터를 객체로 만듭니다.
        const userData = {
          user_id: inputs.user_id,
          password: inputs.password,
          name: inputs.name,
          phone: inputs.phone,
          nickname: inputs.nickname,
          email: inputs.email,
        };
        await axios.post("http://localhost:3002/api/auth/register", userData);
        /*  const response = await axios.post(
          "http://localhost:3002/api/auth/register",
          userData
        ); 이거는 이 response변수를 이용해 
        if (response.data.success) {
  // 회원가입 성공 메시지 출력 또는 다른 처리
  console.log("회원가입 성공:", response.data.message);
}
이런걸 하려고할때, 
*/
        navigate("/login");
      } catch (error) {
        console.error("회원가입 오류:", error);
      }
    }
  };
  const [language, setLanguage] = useState(languageOptions[0]); // 초기 언어 설정
  const customStyles = {
    indicatorContainer: (provided) => ({
      ...provided,
      paddingLeft: 0,
      paddingRight: 0,
    }),
    // control: 전체, singleValue: 선택한값, option: 토글버튼, menu:선택할언어종류쪽
    singleValue: (styles) => ({
      ...styles,
      color: "#FFFFFF",
      fontSize: "15px",
    }),
    control: (styles) => ({
      ...styles,
      display: "flex",
      minWidth: "107px",
      minHeight: "46px",
      justifyContent: "center",
      alignItems: "center",
      gap: "2px",
      borderRadius: "6px",
      backgroundColor: "#3C3C3C",
      border: "1px",
      boxSizing: "border-box",
      padding: "0 8px",
    }),
    option: (styles) => ({
      ...styles,
      padding: 0,
    }),

    indicatorSeparator: () => ({
      display: "none", // 작대기를 없애기 위해 display를 none으로 설정
    }),
    menu: (styles) => ({
      ...styles,
      backgroundColor: "#292929;", // 배경색을 검은색(#000000)으로 설정
    }),
    valueContainer: (styles) => ({
      ...styles,
      padding: 0,
      textAlign: "right",
    }),
  };
  const handleLanguageChange = (selectedOption) => {
    setLanguage(selectedOption);
  };
  return (
    <div className="background">
      <div className="Register_register-form">
        <mini-container>
          {Object.keys(initialInputs).map((fieldName) => (
            <div className="container_input" key={fieldName}>
              <span className="input_title">{inputLabels[fieldName]}</span>
              <form>
                <input
                  className="Login_textfield"
                  type={
                    fieldName === "password"
                      ? "password"
                      : fieldName === "phone"
                      ? "tel"
                      : "text"
                  }
                  name={fieldName}
                  autoComplete="off"
                  placeholder={inputLabels[fieldName]}
                  value={inputs[fieldName]}
                  onChange={handleInputChange}
                />
                {errors[fieldName] && (
                  <span className="error">{errors[fieldName]}</span>
                )}
              </form>
            </div>
          ))}
          <div className="Login_buttons">
            <button className="orange_btn" onClick={handleSubmit}>
              회원가입
            </button>
            <button className="grey_btn">
              <Link to="/login" className="register_text">
                로그인으로 돌아가기
              </Link>
            </button>
          </div>
          <div className="conditions_warning">
            회원가입 버튼을 누르는 것으로 이용약관, 데이터정책에 동의하게 됩니다
            <span className="conditions">이용약관 </span>
            <span className="conditions">데이터정책</span>
          </div>
        </mini-container>
        <mini-container style={{ marginLeft: "64px" }}>
          <div className="Login_right-section">
            <div className="Login_img_language">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="56"
                height="56"
                viewBox="0 0 56 56"
                fill="none"
              >
                <circle cx="28" cy="28" r="28" fill="#3C3C3C" />

                <text
                  x="50%"
                  y="50%"
                  textAnchor="middle"
                  dominantBaseline="middle"
                  className="only_adult"
                >
                  19
                </text>
              </svg>
              <div>
                <Select
                  value={language}
                  onChange={handleLanguageChange}
                  options={languageOptions}
                  styles={customStyles} // 커스텀 스타일 적용
                />
              </div>
            </div>
            <p className="Login_alert">
              *아트그라비아 사이트의 모든 상품(사진 및 영상)은 불법 공유 및 무단
              재배포 또는 상업적으로 사용하실 수 없습니다 이를 어길시 법적인
              처벌을 받습니다. 이 정보내용은 청소년유해매체물로서 [정보통신망
              이용촉진 및 정보보호 등에 관한 법률]및 [청소년 보호법]에 따라 19세
              미만의 청소년이 이용할 수 없습니다. 상호명 : (주)아트그라비아
              회사주소 : 경기도 부천시 신흥로 223 102동 1013호 (중동,
              리슈빌엔클래스) (14548) 사업자등록번호 : 713-87-01993
              통신판매업번호: 제 2021-경기부천-0286 호 대표이사 : 이현수
            </p>
          </div>
        </mini-container>
      </div>
    </div>
  );
};

export default Resister;
