import React, { useState } from "react";
import Slider from "react-slick";
import ModalVideo from "./Slide_modal_video"; // 모달 동영상 컴포넌트를 가져옵니다.

export default function PostSlider(props) {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedVideo, setSelectedVideo] = useState("");
  const [selectedVideoIndex, setSelectedVideoIndex] = useState(null);

  const openModal = (videoUrl, index) => {
    setSelectedVideo(videoUrl);
    setSelectedVideoIndex(index);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setSelectedVideo("");
    setSelectedVideoIndex(null);
    setIsModalOpen(false);
  };

  const { settings } = props;
  const defaultSetting = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 5,
    autoplay: false,
    draggable: false,
  };
  const mergedSettings = { ...defaultSetting, ...settings };
  const videoUrls = props.videoUrls || []; // props로 전달된 이미지 배열 또는 빈 배열 사용
  // 동영상 URL 배열

  return (
    <div>
      <Slider className="slide_copy-imgs" {...mergedSettings}>
        {videoUrls.map((videoUrl, index) => (
          <div key={index} onClick={() => openModal(videoUrl, index)}>
            <video className="copy_video_400">
              <source src={videoUrl} type="video/mp4" alt="video" />
              Your browser does not support the video tag.
            </video>
          </div>
        ))}
      </Slider>
      {isModalOpen && (
        <ModalVideo
          videoUrl={
            selectedVideoIndex !== null ? videoUrls[selectedVideoIndex] : null
          }
          onClose={closeModal}
        />
      )}
    </div>
  );
}
