import { React, useState } from "react";
import axios from "axios";
import "../css/home_combine.css";
import Select from "react-select";
import { Link } from "react-router-dom";
import { TokenAtomState, isLoginSelector } from "../Recoil/Token_atom";
import { useRecoilState, useRecoilValue, useSetRecoilState } from "recoil"; //
import { useLocation, useNavigate } from "react-router";
import { handleLogout } from "../component/logout";
import { userInfoState } from "../Recoil/userData_atom";
const languageOptions = [
  { value: "korean", label: "한국어" },
  { value: "english", label: "English" },
  { value: "japanese", label: "日本語" },
];

const Login = () => {
  const setAccessToken = useSetRecoilState(TokenAtomState);
  const navigate = useNavigate();
  const location = useLocation();
  const from = location?.state?.redirectedFrom?.pathname || "/";
  const [password, setPassword] = useState("");
  const [user_id, setId] = useState("");
  const [isLoggedIn, setIsLoggedIn] = useRecoilState(isLoginSelector);
  const setIsLoggedInState = useSetRecoilState(isLoginSelector);
  const setUserInfo = useSetRecoilState(userInfoState);
  const handleLogin = async () => {
    try {
      // 입력 정보 객체로
      const userData = {
        user_id,
        password,
      };

      // 백엔드 서버의 로그인 엔드포인트에 POST 요청을 보냅니다.
      const res = await axios.post(
        "http://localhost:3003/api/auth/login",
        userData
      );

      if (res.status === 200) {
        console.log("로그인 성공"); // 로그인 성공 메세지 출력

        setAccessToken(res.data.accessToken);
        setUserInfo(res.data.userInfo);

        navigate(from);
      } else {
        console.error("로그인 실패");
      }
    } catch (error) {
      console.error("로그인 오류:", error);
      // 로그인 중에 발생한 오류를 처리합니다. 예를 들어, 사용자에게 오류 메시지를 표시할 수 있습니다.
    }
  };

  const [language, setLanguage] = useState(languageOptions[0]); // 초기 언어 설정
  const handleIdChange = (e) => {
    setId(e.target.value);
  };
  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const customStyles = {
    indicatorContainer: (provided) => ({
      ...provided,
      paddingLeft: 0, // 왼쪽 패딩을 0으로 설정
      paddingRight: 0, // 오른쪽 패딩을 0으로 설정
    }),
    // control: 전체, singleValue: 선택한값, option: 토글버튼, menu:선택할언어종류쪽
    singleValue: (styles) => ({
      ...styles,
      color: "#FFFFFF", // 텍스트 색상을 흰색(#FFFFFF)으로 변경
      fontSize: "15px", // 폰트 크기를 15px로 변경
    }),
    control: (styles) => ({
      ...styles,
      display: "flex",
      minWidth: "107px",
      minHeight: "46px",
      justifyContent: "center",
      alignItems: "center",
      gap: "2px",
      borderRadius: "6px",
      backgroundColor: "#3C3C3C",
      border: "1px",
      boxSizing: "border-box",
      padding: "0 8px",
    }),
    option: (styles) => ({
      ...styles,
      padding: 0,
    }),

    indicatorSeparator: () => ({
      display: "none", // 작대기를 없애기 위해 display를 none으로 설정
    }),
    menu: (styles) => ({
      ...styles,
      backgroundColor: "#292929;", //
    }),
    valueContainer: (styles) => ({
      ...styles,
      padding: 0, // 패딩 제거
      textAlign: "right", // 텍스트를 오른쪽으로 정렬
    }),
  };
  const handleLanguageChange = (selectedOption) => {
    setLanguage(selectedOption);
  };

  return (
    <div className="background">
      <div className="Login_login-form">
        <mini-container>
          <form className="Login_textfields">
            <div className="container_input">
              <span className="input_title">아이디</span>
              <div>
                <input
                  className="Login_textfield"
                  type="text"
                  name="user_id"
                  autoComplete="off"
                  placeholder="아이디"
                  value={user_id}
                  onChange={handleIdChange}
                />
              </div>
            </div>
            <div className="container_input">
              <span className="input_title">비밀번호</span>
              <div>
                <input
                  className="Login_textfield"
                  type="password"
                  name="password"
                  value={password}
                  onChange={handlePasswordChange}
                  autoComplete="off"
                  placeholder="비밀번호"
                />
              </div>
            </div>
          </form>
          <div className="Login_buttons">
            {isLoggedIn ? (
              // 로그인된 경우 로그아웃 버튼을 표시
              <button className="green_btn" onClick={handleLogout}>
                로그아웃
              </button>
            ) : (
              // 로그인되지 않은 경우 로그인 버튼을 표시
              <button className="orange_btn" onClick={handleLogin}>
                로그인
              </button>
            )}
            <button className="grey_btn">
              <Link to="/register" className="register_text">
                회원가입
              </Link>
            </button>
          </div>
          <div className="Login_find-id-ps">
            <Link to="/findAccount" className="register_text">
              아이디 / 비밀번호 찾기
            </Link>
          </div>
        </mini-container>
        <div className="Login_border-row"></div>
        <mini-container>
          <div className="Login_right-section">
            <div className="Login_img_language">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="56"
                height="56"
                viewBox="0 0 56 56"
                fill="none"
              >
                <circle cx="28" cy="28" r="28" fill="#3C3C3C" />
                <text
                  x="50%"
                  y="50%"
                  textAnchor="middle"
                  dominantBaseline="middle"
                  className="only_adult"
                >
                  19
                </text>
              </svg>
              <div>
                <Select
                  value={language}
                  onChange={handleLanguageChange}
                  options={languageOptions}
                  styles={customStyles}
                />
              </div>
            </div>
            <p className="Login_alert">
              *리얼그라비아 사이트의 모든 상품(사진 및 영상)은 불법 공유 및 무단
              재배포 또는 상업적으로 사용하실 수 없습니다 이를 어길시 법적인
              처벌을 받습니다. 이 정보내용은 청소년유해매체물로서 [정보통신망
              이용촉진 및 정보보호 등에 관한 법률]및 [청소년 보호법]에 따라 19세
              미만의 청소년이 이용할 수 없습니다. 상호명 : (주)리얼그라비아
              회사주소 : 경기도 부천시 신흥로 223 102동 1013호 (중동,
              리슈빌엔클래스) (14548) 사업자등록번호 : 123-45-67890
              통신판매업번호: 제 2021-경기부천-0286 호 대표이사 : 홍길동
            </p>
          </div>
        </mini-container>
      </div>
    </div>
  );
};
export default Login;
