import React from "react";

export default function ModalVideo({ videoUrl, onClose }) {
  const handleClickInsideModal = (e) => {
    e.stopPropagation(); // 클릭 이벤트가 모달 밖으로 전파되지 않도록 함
  };
  return (
    <div className="Post_overlay" onClick={onClose}>
      <div className="Post_position">
        <div className="Post_modal-content" onClick={handleClickInsideModal}>
          <div className="Post_left-content">
            <video
              id="modalVideo"
              src={videoUrl}
              type="video/mp4"
              controls
              autoPlay
              className="Video_main"
            >
              Your browser does not support the video tag.
            </video>
          </div>
          <div className="Post_right-content">
            <div className="Work_ppl-model_background">
              <div className="Work_ppl-model">
                <div className="Work_ppl-first">
                  <div className="Work_ppl-image">아</div>
                  <div className="Work_ppl-info">
                    <div className="Work_ppl-nickname">
                      모델명 |
                      <span style={{ fontWeight: "400", marginLeft: "3px" }}>
                        모델
                      </span>
                    </div>
                    <div className="Work_ppl-additional-information">
                      구독자 N명
                    </div>
                  </div>
                </div>
                <div className="Work_ppl-model-follow">
                  <button type="button" className="Follow">
                    Follow
                  </button>
                </div>
              </div>
            </div>
            <div className="Post_border"></div>
          </div>
        </div>
      </div>
    </div>
  );
}
