import React, { useState } from "react";
import axios from "axios"; // Axios를 사용하여 API 요청을 보내기 위해 임포트합니다.
import { userInfoState } from "../Recoil/userData_atom";
import { useRecoilValue } from "recoil";

const Modal_top10 = ({ imageUrl, onClose, currentIndex }) => {
  const [isFollowing, setIsFollowing] = useState(false); // 팔로우 상태를 관리할 상태 변수
  const handleClickInsideModal = (e) => {
    e.stopPropagation(); // 클릭 이벤트가 모달 밖으로 전파되지 않도록 함
  };

  const userInfo = useRecoilValue(userInfoState);
  const handleFollowButtonClick = () => {
    const modelIndex = currentIndex !== null ? currentIndex : "";
    const modelName = `모델${modelIndex}`;

    // 팔로우 상태를 업데이트합니다.
    setIsFollowing(true);

    // 이제 Axios를 사용하여 API 요청을 보냅니다.
    axios
      .post("http://localhost:3005/api/user/follow", {
        user_id: userInfo.user_id,
        follower_name: modelName,
      })
      .then((response) => {
        // API 요청이 성공하면 여기에서 처리
        console.log("Followed successfully");
      })
      .catch((error) => {
        // API 요청이 실패하면 여기에서 처리
        console.error("Error following:", error);
        // 실패할 경우 팔로우 상태를 원래대로 돌려놓을 수 있습니다.
        setIsFollowing(false);
      });
  };
  return (
    <div className="Modal_overlay" onClick={onClose}>
      <div className="Modal_position">
        <div className="Work_modal-content" onClick={handleClickInsideModal}>
          <div className="Work_modal-top-con">
            <img className="Modal_image" src={imageUrl} alt="상세 이미지" />
            <div className="Work_description">
              <div className="Work_description-top">
                <div className="Work_info">
                  <div className="Work_title">Lorem Ipsum</div>
                  <div className="Work_hashtags">
                    <button type="button" className="Work_hashtag">
                      #여친느낌
                    </button>
                    <button type="button" className="Work_hashtag">
                      #여친느낌
                    </button>
                    <button type="button" className="Work_hashtag">
                      #여친느낌
                    </button>
                  </div>
                </div>
                <div className="Work_buy-btn-info">구매 9,000</div>
              </div>
              <div className="Work_btns">
                <div className="Work_btns-left">
                  <div className="Work_btn">
                    <button type="button" className="Work_button-icon">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="24"
                        height="22"
                        viewBox="0 0 24 22"
                        fill="none"
                      >
                        <path
                          d="M11.2693 19.3078L11.2671 19.3058C8.14691 16.479 5.67904 14.2391 3.97315 12.1554C2.28446 10.0927 1.5 8.36677 1.5 6.59401C1.5 3.73107 3.73114 1.5 6.6 1.5C8.23433 1.5 9.82597 2.26744 10.8584 3.47874L12 4.81811L13.1416 3.47874C14.174 2.26744 15.7657 1.5 17.4 1.5C20.2689 1.5 22.5 3.73107 22.5 6.59401C22.5 8.3668 21.7155 10.0928 20.0265 12.1573C18.3206 14.2425 15.853 16.485 12.7331 19.3176C12.7327 19.318 12.7322 19.3184 12.7317 19.3189L12.0038 19.9759L11.2693 19.3078Z"
                          stroke="#666666"
                          strokeWidth="3"
                        />
                      </svg>
                    </button>
                    <div className="Work_button-text">50.8k</div>
                  </div>

                  <div className="Work_btn">
                    <button type="button" className="Work_button-icon">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="17"
                        height="22"
                        viewBox="0 0 17 22"
                        fill="none"
                      >
                        <path
                          d="M14.5714 0H2.42857C1.09286 0 0.0121431 1.1 0.0121431 2.44444L0 22L8.5 18.3333L17 22V2.44444C17 1.1 15.9071 0 14.5714 0ZM14.5714 18.3333L8.5 15.6689L2.42857 18.3333V2.44444H14.5714V18.3333Z"
                          fill="#666"
                        />
                      </svg>
                    </button>
                    <div className="Work_button-text">저장하기</div>
                  </div>
                </div>
                <div className="Work_btns-right">
                  <div className="Work_btn">
                    <button type="button" className="Work_button-icon">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="16"
                        height="22"
                        viewBox="0 0 16 22"
                        fill="none"
                      >
                        <path
                          d="M12 4L10.58 5.42L8.99 3.83V15H7.01V3.83L5.42 5.42L4 4L8 0L12 4ZM16 9V20C16 21.1 15.1 22 14 22H2C0.89 22 0 21.1 0 20V9C0 7.89 0.89 7 2 7H5V9H2V20H14V9H11V7H14C15.1 7 16 7.89 16 9Z"
                          fill="#666666"
                        />
                      </svg>
                    </button>
                    <div className="Work_button-text">공유하기</div>
                  </div>
                </div>
              </div>
              <div className="Work_copy-link"></div>
              <div className="Work_ppl">
                <div className="Work_ppl-model_background">
                  <div className="Work_ppl-model">
                    <div className="Work_ppl-first">
                      <div className="Work_ppl-image">아</div>
                      <div className="Work_ppl-info">
                        <div className="Work_ppl-nickname">
                          {" "}
                          모델명 |
                          <span
                            style={{ fontWeight: "400", marginLeft: "3px" }}
                          >
                            모델{currentIndex !== null ? currentIndex : ""}
                          </span>
                        </div>
                        <div className="Work_ppl-additional-information">
                          구독자 N명
                        </div>
                      </div>
                    </div>
                    <div className="Work_ppl-model-follow">
                      {/* 팔로우 버튼을 누를 수 있는 상태인지 확인하여 버튼을 렌더링 */}
                      {!isFollowing ? (
                        <button
                          type="button"
                          className="Follow"
                          onClick={handleFollowButtonClick}
                        >
                          Follow
                        </button>
                      ) : (
                        <div className="Followed">Followed</div>
                      )}
                    </div>
                  </div>
                </div>
                <div className="Work_ppl-model_background">
                  <div className="Work_ppl-model">
                    <div className="Work_ppl-first">
                      <div className="Work_ppl-image">아</div>
                      <div className="Work_ppl-info">
                        <div className="Work_ppl-nickname">
                          {" "}
                          크리에이터명 |
                          <span
                            style={{ fontWeight: "400", marginLeft: "3px" }}
                          >
                            크리에이터
                          </span>
                        </div>
                        <div className="Work_ppl-additional-information">
                          구독자 N명
                        </div>
                      </div>
                    </div>
                    <div className="Work_ppl-model-follow">
                      <button type="button" className="Follow">
                        Follow
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div className="Work_preview">
                <button type="button" className="Work_preview-item">
                  <img
                    src="/img/slide1_sample2.png"
                    alt="이미지"
                    className="Work_img"
                  />
                </button>
                <button type="button" className="Work_preview-item">
                  <img
                    src="/img/slide1_sample2.png"
                    alt="이미지"
                    className="Work_img"
                  />
                </button>
                <button type="button" className="Work_preview-item">
                  <img
                    src="/img/slide1_sample2.png"
                    alt="이미지"
                    className="Work_img"
                  />
                </button>
              </div>
              <p className="Work_detail">
                6000x4000px 93P 아트그라비아의 창작 콘텐츠는 성기 노출/성관계가
                포함되지 않습니다. 아트그라비아의 콘텐츠를 무단 배포, 2차 가공,
                불법 유통, 콘텐츠 구매대행 등의 불법행위가 적발되면 강력한 법적
                처벌이 이루어집니다. 디지털 콘텐츠의 특성상 구매 후 환불이
                불가합니다. 신중히 구매해 주세요.
              </p>
            </div>
          </div>
          <div className="Work_modal-btm-con">
            <span> </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Modal_top10;
