import React, { useState } from "react";
import "../css/register.css";
import "../css/login.css";
import "../css/findAccount.css";
import { Link } from "react-router-dom";
import Select from "react-select";

const languageOptions = [
  { value: "korean", label: "한국어" },
  { value: "english", label: "English" },
  { value: "japanese", label: "日本語" },
];

const FindAccount = () => {
  const [email, setemail] = useState("");
  const handleemailChange = (e) => {
    setemail(e.target.value);
  };
  const [language, setLanguage] = useState(languageOptions[0]); // 초기 언어 설정
  const handleLanguageChange = (selectedOption) => {
    setLanguage(selectedOption);
  };
  const customStyles = {
    indicatorContainer: (provided) => ({
      ...provided,
      paddingLeft: 0,
      paddingRight: 0,
    }),
    // control: 전체, singleValue: 선택한값, option: 토글버튼, menu:선택할언어종류쪽
    singleValue: (styles) => ({
      ...styles,
      color: "#FFFFFF",
      fontSize: "15px",
    }),
    control: (styles) => ({
      ...styles,
      display: "flex",
      minWidth: "107px",
      minHeight: "46px",
      justifyContent: "center",
      alignItems: "center",
      gap: "2px",
      borderRadius: "6px",
      backgroundColor: "#3C3C3C",
      border: "1px",
      boxSizing: "border-box",
      padding: "0 8px",
    }),
    option: (styles) => ({
      ...styles,
      padding: 0,
    }),

    indicatorSeparator: () => ({
      display: "none", // 작대기를 없애기 위해 display를 none으로 설정
    }),
    menu: (styles) => ({
      ...styles,
      backgroundColor: "#292929;",
    }),
    valueContainer: (styles) => ({
      ...styles,
      padding: 0,
      textAlign: "right",
    }),
  };
  return (
    <div className="background">
      <div className="Find_find-form">
        {" "}
        <mini-container>
          <div className="Login_textfields">
            <div className="container_input">
              <span className="input_title">이메일</span>
              <div>
                <input
                  className="Login_textfield"
                  type="text"
                  id="email"
                  autoComplete="off"
                  placeholder="이메일"
                  value={email}
                  onChange={handleemailChange}
                />
              </div>
            </div>
          </div>
          <div className="Login_buttons">
            {" "}
            <button className="orange_btn">수정코드 보내기</button>
            <button className="grey_btn">
              <Link to="/login" className="register_text">
                로그인으로 돌아가기
              </Link>
            </button>
          </div>
        </mini-container>
        <div className="Login_border-row"></div>
        <mini-container>
          <div>
            <div className="Find_language">
              <div>
                <Select
                  value={language}
                  onChange={handleLanguageChange}
                  options={languageOptions}
                  styles={customStyles} // 커스텀 스타일 적용
                />
              </div>
            </div>
            <p className="Login_alert">
              이메일로 아이디, 비밀번호 수정 코드가 발송됩니다. 비밀번호 수정이
              필요 없을 경우 기존 비밀번호로 로그인해 주세요.
            </p>
          </div>
        </mini-container>
      </div>
    </div>
  );
};

export default FindAccount;
